import Vue from 'vue'
import VueRouter from 'vue-router'
import Embed from '../views/embed/Embed'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/edu'
  },
  {
    path: '/embed',
    name: 'Embed',
    component: Embed
  },
  {
    path: '/iot',
    name: 'IoT',
    component: () => import(/* webpackChunkName: "IoT" */ '../views/iot/IoT'),
  },
  {
    path: '/iot/SailCampus2',
    name: 'SailCampus2',
    component: () => import(/* webpackChunkName: "SailCampus2" */ '../views/iot/solutions/SailCampus2')
  },
  {
    path: '/iot/SailHomeGW2',
    name: 'SailHomeGW2',
    component: () => import(/* webpackChunkName: "SailHomeGW2" */ '../views/iot/solutions/SailHomeGW2')
  },
  {
    path: '/iot/SailCubeGW2',
    name: 'SailCubeGW2',
    component: () => import(/* webpackChunkName: "SailCubeGW2" */ '../views/iot/solutions/SailCubeGW2')
  },
  {
    path: '/rtc',
    name: 'RTC',
    component: () => import(/* webpackChunkName: "about" */ '../views/rtc/RTC')
  },
  {
    path: '/rtc/SailRTC',
    name: 'SailRTC',
    component: () => import(/* webpackChunkName: "SailCubeGW2" */ '../views/rtc/solutions/SailRTC')
  },
  {
    path: '/rtc/SailStream2',
    name: 'SailStream2',
    component: () => import(/* webpackChunkName: "SailCubeGW2" */ '../views/rtc/solutions/SailStream2')
  },
  {
    path: '/edu',
    name: 'EDU',
    component: () => import(/* webpackChunkName: "about" */ '../views/edu/EDU')
  },
  {
    path: '/ebpf',
    name: 'eBPF',
    component: () => import(/* webpackChunkName: "about" */ '../views/ebpf/eBPF')
  },
  {
    path: '/contact',
    name: 'Contact',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/contact/Contact')
  }
]

const router = new VueRouter({
  routes
})

export default router
