import Vue from 'vue'
import {Row, Col, Icon, List, Divider, Space, Carousel, Layout, Menu} from 'ant-design-vue';
import App from './App.vue'
import router from './router'
import 'ant-design-vue/dist/antd.css';
import 'github-markdown-css/github-markdown.css';
import './assets/css/main.css';

Vue.config.productionTip = false

Vue.use(Row)
Vue.use(Col)
Vue.use(Icon)
Vue.use(List)
Vue.use(Divider)
Vue.use(Space)
Vue.use(Carousel)
Vue.use(Layout)
Vue.use(Menu)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
